import { computed, reactive, ref } from 'vue'
import { useStore } from 'vuex'
import { isFunction } from 'utils/is'

const userSelectedLocations = ref([])
const userSelectedDefaultLocation = ref({})

export const useUserLocation = () => {
  const store = useStore()
  const locations = ref([])
  const loading = reactive({
    location: {
      get: false
    }
  })
  const filters = reactive({
    page: 1,
    limit: 10,
    search: '',
    is_additional_info: 1
  })
  const pagination = ref({
    last_page: 1,
    from: 1,
    to: 1,
    total: 0
  })
  const selectedLocations = computed(() =>
    userSelectedLocations.value.map((location) => location.id)
  )
  const selectedDefaultLocation = computed(
    () => userSelectedDefaultLocation.value.id
  )
  const defaultLocations = computed(() => userSelectedLocations.value)
  const setLocations = (locationList = []) => (locations.value = locationList)
  const setPagination = (paginationData = {}) => {
    pagination.value = paginationData
  }
  const setSelectedLocations = (locations) => {
    userSelectedLocations.value = locations
  }
  const selectLocation = (selectedLocation) =>
    !!selectedLocation && userSelectedLocations.value.push(selectedLocation)
  const unselectLocation = (selectedLocation = {}) =>
    !!selectedLocation &&
    (userSelectedLocations.value = userSelectedLocations.value.filter(
      (location) => location.id !== selectedLocation.id
    ))
  const selectDefaultLocation = (selectedLocation) => {
    if (!selectedLocation) return
    userSelectedDefaultLocation.value = selectedLocation
  }
  const getLocations = async (data = {}, config = {}, callback = () => {}) => {
    const getLocationParams = {
      ...filters,
      ...data
    }
    const { isPaginate } = config
    try {
      loading.location.get = true
      const response = await store.dispatch('setting/manageUser/getLocations', {
        params: getLocationParams
      })
      const { data: locationList, ...otherData } = response
      const {
        to,
        total,
        from,
        last_page,
        selected: selectedLocations
      } = otherData
      setPagination({
        from: from || 1,
        to: to || 1,
        total: total || 0,
        last_page: last_page || 1
      })
      if (!isPaginate) {
        setSelectedLocations(selectedLocations)
      }
      setLocations(locationList)
      isFunction(callback) && callback(null)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      loading.location.get = false
    }
  }
  const resetState = () => {
    userSelectedLocations.value = []
    userSelectedDefaultLocation.value = {}
    Object.assign(pagination, {
      last_page: 1,
      from: 1,
      to: 1,
      total: 0
    })
    Object.assign(filters, {
      page: 1,
      limit: 10,
      search: ''
    })
  }
  const unsetSelectedLocations = () => {
    resetState()
  }
  return {
    loading,
    locations,
    defaultLocations,
    filters,
    pagination,
    selectedLocations,
    selectedDefaultLocation,
    selectedDefaultLocationDetailed: userSelectedDefaultLocation,
    getLocations,
    selectLocation,
    unselectLocation,
    selectDefaultLocation,
    resetState,
    unsetSelectedLocations
  }
}
