<template>
  <div
    class="bg-white rounded-lg border border-grey-01 overflow-visible"
    :class="$slots.label ? 'relative' : ''"
    :style="{ maxHeight: `${maxHeight}px` }"
  >
    <div v-if="$slots.label" class="absolute -top-4 left-3 bg-white px-1.5">
      <slot name="label"></slot>
    </div>
    <div class="overflow-auto w-full px-8 py-10">
      <PermissionTreeItem
        :nodes="nodes"
        :checked="checked"
        :name="name"
        :disabled="disabled"
        @select-node="onSelectNode"
      />
    </div>
  </div>
</template>
<script>
import { ref, defineComponent, watch, computed, onMounted } from 'vue'

import PermissionTreeItem from './PermissionTreeItem.vue'

export default defineComponent({
  name: 'PermissionTree',
  components: {
    PermissionTreeItem
  },
  props: {
    items: {
      type: Array,
      default: () => []
    },
    checked: {
      type: String,
      default: 'status'
    },
    name: {
      type: String,
      default: 'name'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    maxHeight: {
      type: [String, Number],
      default: '100'
    }
  },
  emits: ['items-change'],
  setup(props, { emit }) {
    const nodeMapping = (nodes = [], key = null) => {
      return nodes.map((node, nodeIndex) => {
        const currentNode = {
          ...node,
          key: key ? `${key}-${nodeIndex}` : `${nodeIndex}`,
          checked: props.checked ? node[props.checked] == 1 : false,
          indeterminate: false
        }
        currentNode.child = nodeMapping(
          node.child,
          key ? `${key}-${nodeIndex}` : `${nodeIndex}`
        )
        return currentNode
      })
    }
    const nodeMappingParent = (
      nodes = [],
      parents = [],
      parentMap = new Map()
    ) => {
      for (let node of nodes) {
        const hasSubChild = node.child.length > 0
        if (hasSubChild) {
          nodeMappingParent(node.child, [node, ...parents], parentMap)
        }
        parentMap.set(node.key, {
          node,
          parents
        })
      }
      return parentMap
    }
    const updateChildrenNodes = (parentNode, childNodes = []) => {
      for (let childNode of childNodes) {
        childNode.checked = parentNode.checked
        const hasSubChild = childNode.child
        if (hasSubChild) updateChildrenNodes(parentNode, childNode.child)
      }
    }
    const updateParentNodes = (parentNodes = []) => {
      for (let node of parentNodes) {
        const totalCheckedNodes = node.child.filter((childNode) => {
          return childNode.checked
        }).length
        const hasIndeterminate =
          node.child.find((childNode) => childNode.indeterminate) !== undefined
        const isIndeterminate =
          (totalCheckedNodes > 0 && totalCheckedNodes < node.child.length) ||
          hasIndeterminate
        const isUnchecked = totalCheckedNodes === 0

        if (isIndeterminate) {
          node.checked = true
          // node.indeterminate = true
        } else if (isUnchecked) {
          node.checked = false
          // node.indeterminate = false
        } else {
          node.checked = true
          // node.indeterminate = false
        }
      }
    }
    const nodes = ref(nodeMapping(props.items))
    const nodesFlatten = computed(() => nodeMappingParent(nodes.value))
    const onSelectNode = (node) => {
      const selectedNode = nodesFlatten.value.get(node.key).node
      const selectedNodeParents = nodesFlatten.value.get(node.key).parents
      selectedNode.checked = !node.checked
      if (selectedNode.checked && selectedNode.indeterminate)
        selectedNode.indeterminate = false
      updateChildrenNodes(selectedNode, selectedNode.child)
      updateParentNodes(selectedNodeParents)
      emit('items-change', nodes.value)
    }
    watch(
      () => props.items,
      (newNodes) => {
        nodes.value = nodeMapping(newNodes)
        emit('items-change', nodes.value)
      }
    )
    onMounted(() => {
      emit('items-change', nodes.value)
    })

    return {
      nodes,
      onSelectNode
    }
  }
})
</script>
