import Select from 'components/Select.vue'
import Pagination from 'components/Pagination.vue'
import PermissionTree from 'components/ManageUser/PermissionTree.vue'
import SelectLocation from 'components/ManageUser/SelectLocation.vue'

import debounce from 'utils/debounce'

export default defineComponent({
  components: {
    Input,
    Checkbox,
    Fieldset,
    Pagination,
    Select,
    PermissionTree,
    SelectLocation
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        email: null,
        first_name: null,
        last_name: null,
        job_title: null,
        status: 1,
        default_location_id: null,
        default_location_name: null,
        locations: null,
        location_permission_all: null,
        password: null,
        password_score: 0,
        role_id: 1,
        select_all: true,
        menu_list: null,
        menus: []
      })
    },
    edit: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Object,
      default: {
        view_confidential: false
      }
    },
    readonly: {
      type: Boolean, // readonly all form
      default: false
    },
    permissionReadonly: {
      // readonly specific for permission
      type: Boolean,
      default: false
    },
    permissionOnly: {
      type: Boolean,
      default: false
    },
    permissionMaxHeight: {
      type: Number,
      default: 300
    }
  },
  setup(props) {
    const {
      stateFiltered: stateLocation,
      stateLocationPermission,
      loading: loadingLocation,
      query,
      queryLocationPermission,
      locations,
      locationsPremission
    } = useLocations()
    const {
      loading: userLocationLoading,
      locations: userLocations,
      defaultLocations: userDefaultLocations,
      filters: userLocationFilters,
      pagination: userLocationPagination,
      selectedLocations: userSelectedLocations,
      selectedDefaultLocation: userSelectedDefaultLocation,
      getLocations: userLocationsGet,
      selectLocation: userLocationSelect,
      unselectLocation: userUnselectLocation,
      selectDefaultLocation: userSelectDefaultLocation,
      unsetSelectedLocations: userLocationUnsetSelectedLocations,
      resetState: userLocationResetState
    } = useUserLocation()
    const route = useRoute()
    const store = useStore()
    const permission = reactive({
      main_menu: 1,
      setting: 1
    })
    const password = reactive({
      score: 0,
      strength: null,
      show: false
    })
    const data = reactive(props.data)
    const selectedLocation = computed({
      get: () => ({
        id: data.default_location_id,
        name: data.default_location_name
      }),
      set: (val) => {
        data.default_location_id = val ? val.id : null
        data.default_location_name = val ? val.name : null
      }
    })
    const defaultLocations = computed(() => {
      if (
        stateLocation.value.data &&
        stateLocation.value.data &&
        selectedLocationPermission.value &&
        selectedLocationPermission.value.length
      ) {
        return stateLocation.value.data.map((item) => {
          if (selectedLocationPermission.value.includes(item.id)) {
            return {
              ...item,
              disabled: false
            }
          } else
            return {
              ...item,
              disabled: true
            }
        })
      }
      return stateLocation.value ? stateLocation.value.data : []
    })
    const isFocusLocationPermission = ref(false)
    const selectedLocationPermission = ref([])
    const selectedAllLocationPermission = ref(false)

    const getScore = (score) => {
      Object.assign(password, score)
    }

    const rulesAdmin = {
      first_name: { required },
      last_name: { required },
      email: { required, email },
      job_title: { required },
    }
    const rules = {
      first_name: { required },
      last_name: { required },
      email: { required, email },
      default_location_id: { required },
      locations: { required },
      location_permission_all: { required }
    }

    if (!props.edit) {
      rules.password = {
        required,
        passwordValidation: helpers.withMessage(
          'At least 6 characters and include capital letters',
          helpers.regex(/^(?=.*?[A-Z]).{6,}$/)
        )
      }
    }

    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validationAdmin = useVuelidate(rulesAdmin, data, {
      $lazy: true
    })
    const validationGlobal = computed(() => {
      const locations = stateLocation.value.data ? stateLocation.value.data : []
      const locationsPermission = stateLocationPermission.value.data
        ? stateLocationPermission.value.data
        : []
      if (locations.length <= 1 && !props.readonly) {
        return validationAdmin.value
      }
      if (locationsPermission.length <= 1 && !props.readonly) {
        return validationAdmin.value
      }
      return validation.value
    })

    const validate = () => {
      validationGlobal.value.$validate()
      return !validationGlobal.value.$invalid
    }

    const isReadonly = computed(() => props.readonly)
    const isPermissionReadonly = computed(
      () => props.permissionReadonly || isReadonly.value
    )

    const changeMenu = (menu) => {
      if (menu.id) {
        menu.child.forEach((item) => {
          item.child.forEach((subChild) => {
            subChild.status = menu.status
          })
        })
      } else {
        menu.child.forEach((item) => {
          item.status = menu.status
        })
      }
    }

    const handleItemsChange = (items = []) => {
      data.menus = items
    }

    const handleResetDefault = () => {
      if (selectedLocationPermission.value.length) {
        if (selectedLocationPermission.value.length == 1) {
          const location = [...stateLocation.value.data].find(
            (item) => item.id === selectedLocationPermission.value[0]
          )
          if (location) {
            selectedLocation.value = { id: location.id, name: location.name }
            return
          }
        }
      }
      selectedLocation.value = null
    }

    const handleLocationPermission = (items) => {
      selectedLocationPermission.value = items
      data.locations = selectedLocationPermission.value
      handleResetDefault()
    }

    const handleLocationPermissionAll = (value) => {
      selectedAllLocationPermission.value = value
      data.location_permission_all = selectedAllLocationPermission.value ? 1 : 0
      handleResetDefault()
    }

    const handleChangePageUserLocation = async (page) => {
      userLocationFilters.page = page
      await userLocationsGet(
        {
          admin_id: route.params.userId || null
        },
        {
          isPaginate: true
        }
      )
    }
    const handleChangeLimitUserLocation = async (limit) => {
      userLocationFilters.limit = limit
      userLocationFilters.page = 1
      await userLocationsGet(
        {
          admin_id: route.params.userId || null
        },
        {
          isPaginate: true
        }
      )
    }
    const handleSearchUserLocation = debounce(async (search) => {
      userLocationFilters.search = search
      userLocationFilters.page = 1
      await userLocationsGet(
        {
          admin_id: route.params.userId || null
        },
        {
          isPaginate: true
        }
      )
    }, 800)
    const handleSelectUserLocation = (selectedLocation) => {
      const isIncluded = userSelectedLocations.value.includes(
        selectedLocation.id
      )
      if (isIncluded) userUnselectLocation(selectedLocation)
      else userLocationSelect(selectedLocation)
    }
    const handleSelectUserDefaultLocation = (selectedLocation) => {
      userSelectDefaultLocation(selectedLocation)
    }

    // watch(
    //   () => ({
    //     search: query.search
    //   }),
    //   () => {
    //     query.page = 1
    //   }
    // )

    // watch(
    //   () => ({
    //     search: query.search,
    //     page: query.page
    //   }),
    //   debounce(async () => {
    //     await locations(() => {}, route.params.userId || undefined)
    //   }, 500)
    // )

    // watch(
    //   () => ({
    //     searchLocationPermission: queryLocationPermission.search
    //   }),
    //   () => {
    //     queryLocationPermission.page = 1
    //   }
    // )

    // watch(
    //   () => ({
    //     searchLocationPermission: queryLocationPermission.search,
    //     pageLocationPermission: queryLocationPermission.page
    //   }),
    //   debounce(async () => {
    //     await locationsPremission(() => {}, route.params.userId || undefined)
    //     const loaded = queryLocationPermission.loaded.includes(
    //       queryLocationPermission.page
    //     )
    //     if (selectedAllLocationPermission.value) {
    //       stateLocationPermission.data.map((location) =>
    //         selectedLocationPermission.value.push(location.id)
    //       )
    //     } else if (!loaded) {
    //       stateLocationPermission.data.map((location) => {
    //         if (location.is_allowed)
    //           selectedLocationPermission.value.push(location.id)
    //       })
    //       queryLocationPermission.loaded.push(queryLocationPermission.page)
    //     }
    //     data.locations = selectedLocationPermission.value
    //     data.location_permission_all = selectedAllLocationPermission.value
    //       ? 1
    //       : 0
    //   }, 500)
    // )

    onMounted(async () => {
      await userLocationsGet({
        admin_id: route.params.userId
      })
      // await locations(() => {}, route.params.userId || undefined)
      // await locationsPremission(() => {}, route.params.userId || undefined)
      // data.locations = null
      // if (stateLocationPermission && stateLocationPermission.data && stateLocationPermission.data.length) {
      //   selectedLocationPermission.value = []
      //   selectedAllLocationPermission.value = stateLocationPermission.location_permission_all && stateLocationPermission.location_permission_all == 1 ? true : false
      //   stateLocationPermission.data.map(location => {
      //     if (location.is_allowed) selectedLocationPermission.value.push(location.id)
      //   })
      //   data.locations = selectedLocationPermission.value
      //   data.location_permission_all = selectedAllLocationPermission.value ? 1 : 0
      //   queryLocationPermission.loaded.push(queryLocationPermission.page)
      // }
    })

    onUnmounted(() => {
      userLocationResetState()
    })

    return {
      userLocationLoading,
      userLocations,
      userSelectedLocations,
      userLocationPagination,
      userLocationFilters,
      userDefaultLocations,
      userSelectedDefaultLocation,
      data,
      getScore,
      password,
      validationGlobal,
      stateLocation,
      defaultLocations,
      stateLocationPermission,
      loadingLocation,
      selectedLocation,
      isFocusLocationPermission,
      selectedLocationPermission,
      selectedAllLocationPermission,
      query,
      queryLocationPermission,
      validate,
      permission,
      isPermissionReadonly,
      isReadonly,
      changeMenu,
      store,
      route,
      rules,
      handleLocationPermission,
      handleLocationPermissionAll,
      handleItemsChange,
      handleChangePageUserLocation,
      handleChangeLimitUserLocation,
      handleSearchUserLocation,
      handleSelectUserLocation,
      handleSelectUserDefaultLocation
    }
  }
})
</script>
