.input-location-permission {
  @apply border py-4 px-5 w-full border-grey-02 rounded-lg;
}

.input-location-permission.is-focus ~ .input-location-permission--option {
  @apply z-10 visible opacity-100 scale-100;
}

.input-location-permission--option {
  @apply absolute top-full pt-3 left-0 right-0 transition-all duration-300 z-0 invisible opacity-0 transform scale-0;
