<template>
  <div>
    <ul :class="['tree', isChild ? 'tree--child' : '']">
      <li v-for="node of nodes" :key="node.key" class="tree__item">
        <label
          :for="node.key"
          :class="[
            'checkbox',
            'checkbox-default',
            node.indeterminate ? 'checkbox-indeterminate' : '',
            node.checked ? 'checked' : '',
            disabled ? 'checkbox-disabled' : ''
          ]"
        >
          <div class="checkbox__content">
            {{ name ? node[name] : '' }}
          </div>
          <input
            type="checkbox"
            class="checkbox"
            @click="onSelectNode(node)"
            :disabled="disabled"
            :id="node.key"
            :checked="node.checked"
            :indeterminate="node.indeterminate"
          />
          <span class="checkbox__mark"></span>
        </label>
        <PermissionTreeItem
          @select-node="onSelectNode"
          :nodes="node.child"
          :checked="checked"
          :name="name"
          :is-child="true"
          :disabled="disabled"
        />
      </li>
    </ul>
  </div>
</template>
<script>
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'PermissionTreeItem',
  props: {
    nodes: {
      type: Array,
      default: () => []
    },
    checked: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    isChild: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  emits: ['select-node'],
  setup(props, { emit }) {
    const onSelectNode = (node) => {
      emit('select-node', node)
    }
    return {
      onSelectNode
    }
  }
})
</script>
<style lang="scss" scoped>
.tree {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr 1fr;
  &--child {
    @apply flex flex-col ml-10 mt-2;
  }
}
