.checkbox.checkbox-default:not(.checkbox-disabled):hover input:not(:checked) ~ .checkbox__mark {
  border: 1px solid #424242 !important;
}
.checkbox.checkbox-default.checked input ~ .checkbox__mark:after {
  display: block !important;
}
.checkbox.checkbox-default:not(.checked) input ~ .checkbox__mark {
  border: 1px solid #ccc !important;
}
</style>
