.select-location {
  $self: &;
  @apply relative w-full;
  &--disabled {
    #{$self}__button {
      @apply bg-grey-lighten cursor-not-allowed;
      &:focus,
      &:active,
      &:focus-within {
        @apply ring-0;
      }
    }
  }

  &__button {
    @apply px-3 py-2 rounded-lg bg-white border border-grey-01 w-full;
    &:focus,
    &:active,
    &:focus-within {
      @apply ring-1 ring-teal-04;
    }
  }

  &__dropdown {
    @apply bg-white border border-grey-01 rounded-lg mt-2 absolute;
    z-index: 9999;
  }

  &__dropdown-item {
    @apply border-b border-grey-lighten hover:bg-grey-lighten;
    .checkbox {
      @apply py-4 mx-4;
    }
  }

  &__dropdown-search {
    @apply pt-4 pb-1 px-4;
  }

  &__dropdown-item-button {
    @apply w-full p-4 text-left;
    &--active {
      @apply font-semibold;
    }
  }
}
</style>
